/*Regular*/
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
  font-weight: 400;
  font-style: normal; }

/*Bold*/
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
  font-weight: 700;
  font-style: normal; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 15px;
  line-height: 30px;
  font-family: "Open Sans", "Tahoma", sans-serif;
  color: #000000;
  font-style: normal; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.logo {
  width: 86px;
  margin: 0 auto;
  display: block; }

.logo__img {
  width: 100px;
  height: 50px; }

.logo__tablet {
  display: none; }

.logo__desktop {
  display: none; }

@media (min-width: 768px) {
  .logo__mobile {
    display: none; }
  .logo__tablet {
    display: block; } }

@media (min-width: 1050px) {
  .logo__tablet {
    display: none; }
  .logo__desktop {
    display: block; } }

.header {
  margin: 0 auto;
  width: 320px; }
